import { FC } from 'react';

import { Wrapper, Quote, Cite, QuotesSVG } from './BlockQuote.styles';
import { BlockQuoteProps } from './BlockQuote.types';

/**
 * The `<BlockQuote />` component displays a quote with a citation. Any children
 * will be rendered as the quote.
 */
const BlockQuote: FC<BlockQuoteProps> = ({ cite, children }) => (
  <Wrapper>
    <QuotesSVG />
    <Quote>{children}</Quote>
    <Cite>{cite}</Cite>
  </Wrapper>
);

export default BlockQuote;
